import "./leftnav.css";
import "./header.css";
import { navigate } from "gatsby";
import { useState } from 'react';
import { Link } from "gatsby";
import React from "react";
import { graphql } from "gatsby";

const LeftNav = (props) => {
  let classString = 'LeftNav';
  return (
    <div className={classString}>
      <div className="LeftNav__Header">
      <svg xmlns="http://www.w3.org/2000/svg" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="bars" class="svg-inline--fa fa-bars fa-w-14" role="img" viewBox="0 0 448 512"><path fill="currentColor" d="M16 132h416c8.837 0 16-7.163 16-16V76c0-8.837-7.163-16-16-16H16C7.163 60 0 67.163 0 76v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16z"/></svg>
      <span> APIs</span>

      </div>
      <StoplightLinks nodes={props.props}></StoplightLinks>
      </div>
  );
};

const handleAPISelect = e => {


  if (e.target.value.includes(".yml")) {
    const base = "/sl?api=https://bigcommerce.stoplight.io/api/v1/projects/";
    const url = base + "bigcommerce" + "/" + "api-reference" + "/nodes" +  e.target.value + "?branch=" + "master" + "&format=json";
    window.location.href = url;
  }

}

function APIDataList (props)  {
  return (
    <div>
      <form>
      <input type="text" onChange={handleAPISelect} list="apis" id="api-choice" placeholder="APIs"  />
      <datalist id="apis">
        {props.nodes.map((item) =>
          <option value={item.node_uri}>{item.branch_node_name}</option>
        )}
      </datalist>
      </form>
    </div>
  )
}

const StoplightLinks = (nodes) => {

  const base = "/sl?api=https://bigcommerce.stoplight.io/api/v1/projects/";

  return (
    nodes.nodes.map((item) =>

      <Link
        className="LeftNav__link"
        activeClassName="LeftNav__link--active"
        to={base + item.workspace.slug + "/" + item.project_slug + "/nodes" + item.node_uri + "?branch=" + item.branch_slug + "&format=json"}
      >{item.branch_node_name}</Link>
    ));
};

export default LeftNav;